.header-outer-container {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(20, 20, 20);
  /*background-color: transparent;*/
  height: 10vh;
  color: white;
  transition: all 0.5s;
  transition-timing-function: ease-in;
  z-index: 1;
  width: 100%;
  /*  background-color: red;*/
}
.nav__black {
  background-color: black;
}

.header_container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-right ul {
  width: 90%;
  display: flex;
  justify-content: space-between;
  list-style: none;
  cursor: pointer;
  gap: 20px;
}
.header-left ul {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  cursor: pointer;
  gap: 20px;
}
.header-left li,
.header-right li {
  white-space: nowrap;
  cursor: pointer;
}
